<template>
  <h3>Add Formula</h3>
  <p>name: {{ data.name }}</p>
  <p>alias: {{ data.alias }}}</p>
  <p>age: {{ data.age }}</p>
  <p>Job: {{ data.job.type }}</p>
  <p>salary: {{ data.job.salary }}</p>
  <p>Hobby: {{ data.hobby }}</p>
  <p>num: {{ data.num }}</p>
  <p>Three C: {{ data.job.a.b.c }}</p>
  <el-button type="primary" @click="changeInfo">
    changeInfo
  </el-button>
</template>

<script>
import { h, ref, reactive } from 'vue'
export default {
  name: "AddFormula",
  setup() {
    let data = reactive({
      name: '韩梅梅',
      alias: 'Han meimei',
      age: 18,
      job: {
        type: 'teacher',
        salary: '40k',
        a: {
          b: {
            c: 100
          }
        }
      },
      num: 8080,
      hobby: ['抽烟', '喝酒', '烫头']
    })
    
    function changeInfo() {
      data.name = 'Li leilei'
      data.age++
      data.job.type = 'Move Birck'
      data.job.a.b.c++
      data.hobby[0] = '抽两根烟'
    }

    return {
      data,
      changeInfo
    }
  
    return () => h('h1', 'This is render content.')
  }
}
</script>

